import  axios from "axios"
import router from '@/router';


export default {
    name: "Register",
    data() {
        return {
            awaitMsg : "",
            validationsRegister: "",
            username : "",
            email: "",
            adress: "",
            phone: "",
            password: "",
            confirmPassword: "",
            userCreated: "",
            baseURL: "https://backend-turtle-pex86ccc5-tiagobonito44-hotmailcom.vercel.app/register"
        }
    }, 

    methods: {
        createUser() {
            // tentando criar um usuario
            this.awaitMsg = "Aguarde, estamos tentando criar sua conta."
            this.validations() // validaÃ§Ãµes

            if(this.validationsRegister) {      // se tiver erros, pare o codigo
                this.awaitMsg = ""
                return
            }


            const fields = {   // definindo os mesmos codigos
                username: this.username,
                email: this.email,
                adress: this.adress, 
                phone: this.phone,
                password: this.password,
                userCreated: ""
            }


            // fazendo a criaÃ§Ã£o, pegando o endereÃ§o e usando os campos 
            axios.post(`${this.baseURL}`, fields)
            .then((res) => {
                console.log(JSON.stringify(res.data))
                this.userCreated = "Usuario criado com sucesso, se divirta! âœ”ðŸ’š"
                setTimeout(() => {
                    router.push( {name: "login"} )
                }, 4000)
            })
            .catch((err) => {
                console.log(err)
            })

           
        },

        // auto explicativo
        validations() {


            if(!this.username && !this.email && !this.adress || !this.phone || !this.password || !this.confirmPassword) {
               return this.validationsRegister = "Preencha todos os campos para prosseguir"
            }

            if(this.username.length >= 12) {
                return this.validationsRegister = "Seu apelido sÃ³ pode ter 12 dÃ­gitos"
            }

            if (!this.email.includes(".com")) {
                this.validationsRegister = "O e-mail informado nÃ£o contÃ©m a parte '.com'";
                return;
            }


            if(this.adress.length >= 15) {
                return this.validationsRegister = "Nacionalidade excessivamente grande"
            }

           
            if(this.password.length < 8) {
                return this.validationsRegister = "Sua senha precisa ser maior que 8 dÃ­gitos"
            }       


            if(this.phone.length < 8 ) {
                return this.validationsRegister = "Seu numero precisa ser maior que 8 dÃ­gitos"
            }

            if(this.password !== this.confirmPassword) {
                return this.validationsRegister = "Senhas nÃ£o batem, tente novamente"
            } 

          

    
        }
    }
}